.ttb-flex-row {
  position: relative;
  display: flex;
  width: 100%;
  flex-direction: row;

  >div {
    display: flex;
    flex: 1;
    align-items: center;
  }

  >.ttb-flex-shrink {
    display: flex;
    flex: 0 0 auto;
  }

  .ttb-flex-block {
    display: inline-block;
  }
}

@media screen and ( max-width: 480px ) {
  .ttb-flex-row {
    flex-direction: column;
  }
}